import { Component, OnInit } from '@angular/core';
import { Router, NavigationExtras } from '@angular/router';
import { LoopBackConfig, LoopBackAuth, BooksApi,App_usersApi, Contractor_profileApi} from '../../../../shared/loopback-sdk';
import * as moment from 'moment'; //for date manupalation
import {PaginatorModule} from 'primeng/primeng';
import { Lightbox, IAlbum, LightboxConfig } from 'angular2-lightbox';
import { CookieService } from 'angular2-cookie/core';

declare var hostname;
declare var sdkPrefix;
@Component({
  templateUrl: 'contractor.component.html'
})
export class ContractorComponent {
	createdByErrors: boolean=false;
	busy:any;
	contractorData:any = [];
	display:boolean = false;
	userList:any;
	ifEmpty:boolean = false;
	userDetails:any;
	rolename:any;
	loading:boolean = true;
	_albums:any = [];
	exportData:any = [];
	filteredUserFullName: any;
	allUsersFullName: any;
	contractorStatus:any="";
	contractorName:any;
	createdAt:any;
	maxDate:Date;

	//variables for filter & pagination
	createdAtSearch:any;
	contractorNameSearch:any;
	contractorStatusSearch:any="";
	paginationVal:any=true;
	perPageLimit:Number = 10;
	offset:Number=0;
	total:number;

	constructor(private uApi:App_usersApi, private booksApi:BooksApi,  private _cookieService: CookieService, private router: Router, private loopAuth:LoopBackAuth,private _lightbox: Lightbox,private contractorProfileApi : Contractor_profileApi) {
		this.userDetails = this.loopAuth.getCurrentUserData();
		this.rolename = this.userDetails.userDet.roles[0].name;
		this.maxDate = new Date();
	}

	ngOnInit(): void {
		this.getCount();
	}

	getCount(){
		this.loading = true;

		if(this.createdAt){
			this.createdAt = moment(this.createdAt).format("YYYY-MM-DD hh:mm:ss.SSS");
		}

		var dataArrObj = {
			"name_of_contractor":this.contractorNameSearch,
			"created_date":(this.createdAtSearch != null && this.createdAtSearch != '' && this.createdAtSearch != undefined)?this.createdAtSearch:0,
			"status":this.contractorStatusSearch,
			"page":this.offset,
			"limit":this.perPageLimit
		}


		this.contractorProfileApi.getContractorsCount(dataArrObj).subscribe(

			data=>{
				
				this.total = data['result'][0]['total'];
				if(this.total <= 10){
					this.paginationVal = false;
				}else{
					this.paginationVal = true;
				}
				this.getData(dataArrObj);
			},
			err=>{

			},
			()=>{

			}
		)
	}
	// editProd(id){
	// 	let navigationExtras: NavigationExtras = {
	// 		queryParams: {
	// 			"id": id,
	// 		}
	// 	};
	// 	this.router.navigate(["edit-hpb"], navigationExtras);
	// }

	// viewProd(id){
	// 	let navigationExtras: NavigationExtras = {
	// 		queryParams: {
	// 			"id": id,
	// 		}
	// 	};
	// 	this.router.navigate(["view-hpb"], navigationExtras);
	// }

	showDialog() {
		this.display = true;
	}

	paginate(event) {
        this.contractorData = [];
		this.offset=event.page;
		var user_id;
		if(this.rolename == "$ra"){
			user_id = this.userDetails.userDet.id;
		}else{
			this.rolename = "";
		}

		this.paginationVal=true;
		if(this.createdAt){
			this.createdAt = moment(this.createdAt).format("YYYY-MM-DD hh:mm:ss.SSS");
		}
		var dataArrObj = {
			"name_of_contractor":this.contractorNameSearch,
			"created_date":this.createdAtSearch,
			"status":this.contractorStatusSearch,
			"page":this.offset,
			"limit":this.perPageLimit
		}

		this.getData(dataArrObj);
	}

	searchData(){
		this.offset = 0;
		this.paginationVal=false;
		this.ifEmpty = false;
		this.contractorData =[];
		this.contractorNameSearch = this.contractorName;
		this.createdAtSearch = (this.createdAt != null && this.createdAt != '' && this.createdAt != undefined)?new Date(this.createdAt).getTime():0,
		this.contractorStatusSearch = this.contractorStatus;
		this.getCount();
	}

	resetPage(){
		this.offset = 0;
		this.paginationVal=false;
		this.createdByErrors = false;
		this.contractorStatus = "";
		//this.bookName = "";
		this.createdAt = "";
		this.createdAtSearch = "";
		this.contractorStatusSearch = "";
		this.contractorNameSearch = "";
		this.contractorName = "";
		this.getCount();
	}

	addContractor(){
		this.router.navigate(["contractor/add-contractor"]);
	}

	getData(dataArrObj){
		let loginFlagToken = this._cookieService.get(sdkPrefix+'id');
		this._albums = [];
		this.loading = true;
		this.contractorProfileApi.get_contractor(dataArrObj).subscribe(
		//this.busy=this.booksApi.getBooks(dataArrObj).subscribe(
			data=>{ 
				

				this.loading = false;
				var totalData = data.result.length;
				this.ifEmpty = false;
				if(totalData == 0){
					this.ifEmpty = true;
				}
				for(var i=0; i<totalData; i++){
					if(data.result[i].status == 1){
						data.result[i].status = 'Active';
					}else{
						data.result[i].status = 'Inactive';
					}
					if(data.result[i].contractor_photo){
						if(data.result[i].contractor_photo != null && data.result[i].contractor_photo != undefined && data.result[i].contractor_photo != ""){
							data.result[i].contractor_photo = hostname+"/api/containers/contractorImage/download/"+data.result[i].contractor_photo+'?access_token='+loginFlagToken;
						}else{
							data.result[i].contractor_photo = "assets/img/photo.jpg";
						}
						
					}else{
						data.result[i].contractor_photo = "assets/img/photo.jpg";
					}
					if(data.result[i].created_date != null  && data.result[i].created_date != undefined){
						data.result[i].created_at = moment(data.result[i].created_date).format("DD-MMM-YYYY");
					}else{
						data.result[i].created_at = "";
					}
					
					this._albums.push(
						{
							src:data.result[i].contractor_photo,
							thumb: data.result[i].contractor_photo
						}
					);
				}
				this.contractorData = data.result;

				//console.log("contractorData", this.contractorData);
			},
			err=>{
				this.loading = false;
			},
			()=>{
				this.loading = false;
			}
		)
	}

	// filterFullName(event){ 
	// 	let query = event.query;
	// 	this.busy = this.createdApi.getCreatedBy("hpb_info_tbl",this.perPageLimit,"",query).subscribe((dataRes)=>{
	// 		this.allUsersFullName = dataRes['result'];
	// 		this.filteredUserFullName = this.filterAutocompleteData(query, this.allUsersFullName,'sph_name');
	// 	});
	// }

	// filterAutocompleteData(query, filterAllData: any[],keyName):any[] {
	// 	//in a real application, make a request to a remote url with the query and return filtered results, for demo we filter at client side
	// 	let filtered : any[] = [];
	// 	for(let i = 0; i < filterAllData.length; i++) {
	// 		let currData = filterAllData[i];
	// 			if(query=='' || query==null){
	// 				filtered.push(currData);
	// 				if(i==this.perPageLimit){
	// 					break;
	// 				}
	// 			}else if(currData[keyName].toString().toLowerCase().includes(query.toLowerCase())) {
	// 				  filtered.push(currData);
	// 			}
	// 	} 
	// 	return filtered;
	// }

	

	open(index: number): void {
		// open lightbox
		this._lightbox.open(this._albums, index, { positionFromTop: 60 });
	}
}


